.create-todo-form {
  margin: 0 auto;
  border-radius: 4px;
  border: 1px solid teal;
  width: 300px;
  height: 300px;
  background-color: white;
}
.create-todo-form .textarea-form {
  margin: 8px auto;
  display: block;
  height: 80%;
  width: 80%;
}

.create-todo-form .form-buttons-container {
  display: flex;
  justify-content: space-between;
  padding: 8px;
}

.create-todo-form .form-button {
  padding: 8px;
  border: 1px solid black;
  border-radius: 2px;
}

.create-todo-form .form-button--cancel {
  border: none;
}

.create-todo-form .form-button--submit {
  background-color: teal;
}